$(document).ready(function(){
	var audio = document.getElementById("audio");
	var audioElement = document.getElementById("audio").setAttribute('src', genieTypeAudio);
	//console.log("audio:"+audio);
	//audioElement.load();
	var isSafari = !!navigator.userAgent.match(/Version\/[\d\.]+.*Safari/);
	var ua = navigator.userAgent.toLowerCase();
	var isAndroid = ua.indexOf("android") > -1;
	
	$("#animateimg").attr("src",genieTypeVoice);
	
	 genieButtonOn();	
	if(isSafari || isAndroid){ /*Check for safari*/
	//if(isSafari ){ /*Check for safari*/
		//console.log("this is safari");
		genieButtonOff();
		$("#genieimg").animate( {right: "-200px"}, 10 );		
	}
	
	else{
		//console.log("this is not safari");
		setTimeout(function(){
			//console.log("running");			
		},500);
	}
	
	/*When audio can be playthrough*/
	document.getElementById("audio").oncanplaythrough = function () {
		  //console.log("sds");	
		  this.play();
		  $("#animateimg").attr("src",geniepaly);
	}
	
	/*On audio ended*/
	document.getElementById("audio").onended = function() {
		//console.log("audio ended");
		audio.pause();
		$("#animateimg").attr("src",genieTypeVoice);
		setTimeout(function(){			
			$("#genieimg").animate( {right: "-200px"}, 500 );
		},1000);
		genieButtonOff();
	};
	
	/*on playing*/
	document.getElementById("audio").onplaying = function () {
        $("#animateimg").attr("src",geniepaly);
    };
	
	
});
  
 window.onload=function(){	
	
		
	
	$(document).ready(function(){
		$("#genie-on-off-switch").click(function(){
        	$("#genie-on-off-switch").toggleClass("genie-btn-on");
			if($( this ).text()=="OFF"){
					$( this ).html("ON");
					setTimeout(function (){ 
					audio.currentTime = 0;
					audio.play();
				}, 500);
			
			 	$("#genieimg").animate( {right: "10px"}, 500 );
				
			}
			
		else
			{	
				$(this ).html("OFF");
				audio.pause();
				$("#animateimg").attr("src",genieTypeVoice);
				setTimeout(function(){
				$("#genieimg").animate( {right: "-200px"}, 500 );
				},1000);
			}
		});
	});
	
	}; 
	  
	  function genieButtonOn() /*show off button*/
	  {
		   $("#genie-on-off-switch").addClass("genie-btn-on");
		   $("#genie-on-off-switch").html("ON");
	  }

	  function genieButtonOff() /*show on button*/
	  {
		   $("#genie-on-off-switch").removeClass("genie-btn-on");
		   $("#genie-on-off-switch").html("OFF");
	  }
		
